import React, { Component } from 'react';

import { EthereumService } from '../services/EthereumService';

import Content from './Content';
import NavBar from './NavBar';
import Message from './Message';
import { Subscription } from 'rxjs';

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      opacity: 0.25,
      activeItem: '',
      message: '',
      error: '',
      contracts: {
        isLoaded: false,
        initError: false,
        walletAddress: '0x00',
        lastHarvestBlockNumber: 0,
        endOfGrowingCycle: 0,
        cornComposted: 0,
        ethBalance: 0,
        landBalance: 0,
        cornBalance: 0,
        cornToHarvest: 0,
        landInFarm: 0,
        farmMaturityBoost: 0,
        farmCompostBoost: 0,
        landSymbol: 'LAND',
        cornSymbol: 'CORN',
        transAmount: 0,
        isAuthorized: false,
        burnRequiredTo10x: 0,
        myRatioVsGlobalRatio: 0
      }
    };

    this.subscription = new Subscription();
    this.ethereumService = new EthereumService();
    this.selectActiveItem = this.selectActiveItem.bind(this);
    this.showMessage = this.showMessage.bind(this);
    this.hideMessage = this.hideMessage.bind(this);
  }

  componentDidMount() {
    var eventSubscription = this.ethereumService.events.subscribe(
      event => {
        if (event) {
          this.showMessage(event);
        }
        this.setState({ contracts: this.ethereumService.contracts });
      },
      error => this.showMessage({ message: 'Unexpected error.', error: error })
    );

    this.subscription.add(eventSubscription);

    var errorSubscription = this.ethereumService.errors.subscribe(
      event => this.showMessage(event),
      error => this.showMessage({ message: 'Unexpected error.', error: error })
    );

    this.subscription.add(errorSubscription);
  }

  showMessage(event) {
    this.setState({
      message: event.message,
      messagePending: event.pending,
      error: event.error
    });
  }

  hideMessage() {
    this.setState({
      message: ''
    });
  }

  componentWillUnmount() {
    this.subscription.unsubscribe();
  }

  selectActiveItem(itemId) {
    this.setState({ activeItem: itemId });
  }

  render() {
    return (
      <div id="app">
        {this.state.initError && (
          <section className="hero">
            <div className="hero-body">
              <div className="container">
                <h1 className="title">No Wallet</h1>
                <h2 className="subtitle">
                  <a href="https://metamask.io">Metamask</a> or equivalent required to access this page.
                </h2>
              </div>
            </div>
          </section>
        )}

        {!this.state.initError && (
          <div>
            <Message
              message={this.state.message}
              hide={this.hideMessage}
              className="is-primary"
              className={`${this.state.error ? 'is-danger' : this.state.messagePending ? 'is-warning' : 'is-success'}`}
            />

            <div className="hero is-fullheight">
              {/* <div className="is-vcentered hero is-fullheight-with-navbar"> */}
              <NavBar
                contracts={this.state.contracts}
                ethereumservice={this.ethereumService}
                activeItem={this.state.activeItem}
                onNavigated={this.selectActiveItem}
              />

              <Content
                contracts={this.state.contracts}
                ethereumservice={this.ethereumService}
                activeItem={this.state.activeItem}
                onNavigated={this.selectActiveItem}
              />

              <div className="hero-foot"></div>
            </div>
          </div>
        )}
      </div>
    );
  }
}

export default App;
