import { ethers, providers } from 'ethers';
import { Subject } from 'rxjs';
import WalletConnectProvider from '@walletconnect/web3-provider';
import Web3Modal from 'web3modal';
// import Fortmatic from "fortmatic";
// import Portis from "@portis/web3";
// import Torus from "@toruslabs/torus-embed";

import landToken from '../contracts/Land.json';
import cornToken from '../contracts/Corn.json';
//import cornToken from '../contracts/Wheat.json';

const landContractAddress = '0x112b2060E01000194d13aF3B15874fF5599cD0f6';
const cornContractAddress = '0x4958e12a3D8b2332C2d153C3390dE7630Cd6c403'; // CORN
//const cornContractAddress = '0x78bD4032C1559F3f1dEA6059C30d4005A26295aa'; //WHEAT

const parseValue = (value, units, decimals) => {
  return value ? parseFloat(ethers.utils.formatUnits(value, units)).toFixed(decimals) : 0;
};

const parseBalance = value => {
  return value ? ethers.utils.formatEther(value) : 0;
};

const supportedNetworks = ['KOVAN'];

const providerOptions = {
  walletconnect: {
    package: WalletConnectProvider,
    options: {
      infuraId: 'b78e83d4ad6f48aaa59a8222780e79f2'
    }
  }
  // portis: {
  //   package: Portis,
  //   options: {
  //     id: "PORTIS_ID"
  //   }
  // },
  // fortmatic: {
  //   package: Fortmatic,
  //   options: {
  //     key: "Fortmatic"
  //   }
  // },
  // torus: {
  //   package: Torus,
  // },
};

const web3Modal = new Web3Modal({
  cacheProvider: true,
  providerOptions
});

export class EthereumService {
  constructor() {
    this.reset();

    this.events = new Subject();
    this.errors = new Subject();

    this.connectWallet();
  }

  reset() {
    if (this.contracts && this.contracts.walletAddress) {
      this.removeEventHandlers(this.contracts.walletAddress);
    }

    this.contracts = {
      isAuthorized: false,
      isPending: false
    };

    this.hasError = false;
    this.errorMessage = '';

    this.land = null;
    this.corn = null;
  }

  resetConnectedWallet() {
    web3Modal.clearCachedProvider();
    this.reset();
    this.connectWallet();
  }

  connectWallet() {
    web3Modal.connect().then(provider => {
      this.provider = new providers.Web3Provider(provider);
      this.connect();
      this.events.next();
    });
  }

  connect() {
    try {
      this.signer = this.provider.getSigner();
      this.initAccount();
      this.setupEventHandlers();
    } catch (e) {
      console.log(e);
      this.initError = true;
    }
  }

  initAccount() {
    // TODO: check network, also contract

    this.reset();

    this.land = new ethers.Contract(landContractAddress, landToken.abi, this.signer);
    this.corn = new ethers.Contract(cornContractAddress, cornToken.abi, this.signer);
  }

  loadBalances() {
    if (this.provider.network === 'undefined' || this.provider.network === 'undefined') {
      this.errors.next({
        message: 'Error loading farm',
        error: 'Error loading farm'
      });
      return new Promise((resolve, reject) => {});
    }

    if (!supportedNetworks.includes(this.provider.network.name.toUpperCase())) {
      this.errors.next({
        message: 'Error loading farm, unsupported network: ' + this.provider.network.name,
        error: 'Unsupported network: ' + this.provider.network.name
      });
      return new Promise((resolve, reject) => {});
    }

    return this.signer.getAddress().then(address =>
      Promise.all([
        //0
        address,
        //1
        this.provider.getBalance(address),
        //2 -getAddressDetails - [0]blockNumber, [1]cornBalance, [2]harvestAmount, [3]addressMaturityMultiplier, [4]addressFertilizerMultiplier, [5]globalPlantedAmount, [6]globalFertilizedAmount
        this.corn.getAddressDetails(address),
        //3 -struct AddressLock {[0]Amount of LAND used, [1]How much CORN has been composted, [2] When was the farm built, [3]When was the last time this address harvested CORN?, [4] Who is allowed to harvest on behalf of this address
        this.corn.farms(address),
        //4 -getAddressTokenDetails - [0] block.number, [1]isCornOperator, [2] landBalance, [3] myRatio (MyComposted / LandInFarm), [4] globalRatio (globalComposted / globalLandInFarm)
        this.corn.getAddressTokenDetails(address)
      ]).then(
        response => {
          this.contracts = {
            landAddress: landContractAddress,
            cornAddress: cornContractAddress,
            isConnected: true,
            walletAddress: response[0],
            delegatedAddress: response[0],
            isAuthorized: response[4] ? response[4][1] : null,
            ethBalance: parseValue(response[1], 18, 2),
            blockNumber: parseValue(response[2] ? response[2][0] : null, 0, 0),
            lastHarvestBlockNumber: parseValue(response[3] ? response[3][3] : null, 0, 0),
            farmBuiltBlockNumber: parseValue(response[4] ? response[4][2] : null, 0, 0),
            landBalance: parseValue(response[4] ? response[4][2] : null, 18, 0), //2),
            landBalanceTrue: parseBalance(response[4] ? response[4][2] : null),
            landInFarm: parseValue(response[3] ? response[3][0] : null, 18, 0), //2),
            cornBalance: parseValue(response[2] ? response[2][1] : null, 18, 4),
            cornBalanceTrue: parseBalance(response[2] ? response[2][1] : null),
            cornToHarvest: parseValue(response[2] ? response[2][2] : null, 18, 8),
            cornComposted: parseValue(response[3] ? response[3][1] : null, 18, 2),
            farmMaturityBoost: parseValue(response[2] ? response[2][3] : null, 4, 2),
            farmCompostBoost: parseValue(response[2] ? response[2][4] : null, 4, 2),
            myRatio: parseValue(response[4] ? response[4][3] : null, 4, 2),
            globalRatio: parseValue(response[4] ? response[4][4] : null, 4, 2),
            globalComposted: parseValue(response[2] ? response[2][6] : null, 18, 2),
            globalLandInFarm: parseValue(response[2] ? response[2][5] : null, 18, 2)
          };

          // Only calculate this stuff when there is a farm
          if (this.contracts.landInFarm > 0) {
            // Calculates the number of blocks until the end of the growing cycle: TODO: can we add the constant to the contract???
            this.contracts.endOfGrowingCycle = parseInt(this.contracts.lastHarvestBlockNumber) + parseInt(5760); //CORN
            //            this.contracts.endOfGrowingCycle = parseInt(this.contracts.lastHarvestBlockNumber) + parseInt(11520); //WHEAT

            // Calculates the approx. number of seconds until the end of the growing cycle (Kovan is 4secs = 4000, Mainnet is 13 secs = 13000)
            this.contracts.growingCycleCountDownSecs =
              4000 * (parseInt(this.contracts.endOfGrowingCycle) - parseInt(this.contracts.blockNumber));
            // Decides how to present the countdown or complete message
            if (this.contracts.growingCycleCountDownSecs > 0) {
              // Convert seconds to days. hours, minutes, seconds and store
              var growingCycleTime = new Date(this.contracts.growingCycleCountDownSecs);
              var growingCycleDays = parseInt(growingCycleTime.toISOString().substr(8, 2)) - 1;
              if (growingCycleDays === 1) {
                var growingCycleDaysLabel = '1 day ';
              } else if (growingCycleDays > 1) {
                growingCycleDaysLabel = growingCycleDays + ' days ';
              } else {
                growingCycleDaysLabel = '';
              }
              this.contracts.growingCycleCountDown =
                growingCycleDaysLabel + growingCycleTime.toISOString().substr(11, 8);
            } else {
              this.contracts.growingCycleCountDown = 'COMPLETE';
            }
            // Calculate how my corn/land compost ratio compares to the global average
            this.contracts.myRatioVsGlobalRatio = parseFloat(
              this.contracts.myRatio / this.contracts.globalRatio,
              4
            ).toFixed(2);

            // The formula to calculate how much to crop harvest to get to 10x compost boost is:
            //     b = (−9 a f + c d) / (−c + 9 f) where:
            //     CornToCompost =(−9*(globalCornComposted*myLandinFarm)+(globalLandinFarms*myCornComposted)) / (−globalLandinFarms+9myLandinFarm)
            //     a = this.contracts.globalComposted
            //     c = this.contracts.globalLandInFarm
            //     d = this.contracts.cornComposted
            //     f = this.contracts.landInFarm
            if (this.contracts.farmCompostBoost < 10) {
              this.contracts.burnRequiredTo10x = Math.abs(
                (-9 * this.contracts.globalComposted * this.contracts.landInFarm +
                  this.contracts.globalLandInFarm * this.contracts.cornComposted) /
                  (-this.contracts.globalLandInFarm + 9 * this.contracts.landInFarm)
              );
            } else {
              this.contracts.burnRequiredTo10x = 'OVER COMPOSTED';
            }
          }
        },
        error => this.errors.next({ message: 'Error loading farm', error: error })
      )
    );
  }

  removeEventHandlers() {
    this.provider.provider.removeAllListeners();
    this.provider.removeAllListeners();
    this.corn.removeAllListeners();
  }

  setupEventHandlers() {
    var refresh = () =>
      this.loadBalances().then(() => {
        this.events.next();
      });

    var update = message =>
      this.loadBalances().then(() => {
        this.events.next({ message: message });
      });

    this.provider.provider.on('networkChanged', networkId => {
      this.connect();
      refresh();
    });

    this.provider.provider.on('accountsChanged', accounts => {
      // eslint-disable-next-line
      if (accounts === undefined || accounts == 0) {
        this.resetConnectedWallet();
        window.location.reload();
      } else {
        this.connect();
        refresh();
      }
    });

    this.provider.provider.on('chainChanged', networkId => {
      this.connect();
      refresh();
    });

    this.provider.on('block', currentBlock => {
      refresh();
    });

    this.signer.getAddress().then(address => {
      this.corn.on(
        {
          address: address,
          topics: [ethers.utils.id('AuthorizedOperator(address,address)')]
        },
        (cornContractAddress, walletAddress) => {
          var message = 'Planning permission granted';
          update(message);
        }
      );

      this.corn.on(
        {
          address: address,
          topics: [ethers.utils.id('Allocated(address,uint256,address,uint256,uint256)')]
        },
        (walletAddress, currentBlock, delegatedAddress, plantedAmount, burnedAmountIncrease) => {
          var message = 'Your LAND is allocated to farm';
          update(message);
        }
      );

      this.corn.on(
        {
          address: address,
          topics: [ethers.utils.id('Released(address,uint256,uint256)')]
        },
        (walletAddress, landInFarm, burnedAmountDecrease) => {
          var message = 'Your LAND has been released from a farm';
          update(message);
        }
      );

      this.corn.on(
        {
          address: address,
          topics: [ethers.utils.id('Harvested(address,uint256,address,address,uint256,uint256)')]
        },
        (walletAddress, blockNumber, delegatedAddress, preBlockNumber, cornToHarvest) => {
          var message = 'Your CORN has been harvested';
          update(message);
        }
      );

      this.corn.on(
        {
          address: address,
          topics: [ethers.utils.id('Composted(address,address,uint256)')]
        },
        (walletAddress, delegatedAddress, fertilizedAmount) => {
          var message = 'Your CORN has been composted & used to fertilize your farm';
          update(message);
        }
      );
    });
  }

  approveContract() {
    this.loadBalances().then(() => {
      this.land.authorizeOperator(cornContractAddress, { from: this.contracts.walletAddress }).then(
        () => this.events.next({ message: 'Planning permission is pending', pending: true }),
        error => this.errors.next({ message: 'Error providing planning permission', error: error })
      );
    });
  }

  allocateLand(amount) {
    this.loadBalances().then(() => {
      var parsedAmount = ethers.utils.parseEther(amount.toString());
      this.corn.allocate(this.contracts.walletAddress, parsedAmount).then(
        () => this.events.next({ message: 'Your LAND is being allocated to a farm', pending: true }),
        error =>
          this.errors.next({
            message: 'Error allocating LAND - To make a bigger farm, ensure you release LAND first',
            error: error
          })
      );
    });
  }

  releaseLand() {
    this.loadBalances().then(() => {
      this.corn.release().then(
        () => this.events.next({ message: 'Pending release of LAND', pending: true }),
        error => this.errors.next({ message: 'Error releasing LAND', error: error })
      );
    });
  }

  harvestCorn() {
    this.loadBalances().then(() => {
      this.provider.getBlockNumber().then(blockNumber => {
        this.corn.harvest(this.contracts.walletAddress, this.contracts.walletAddress, blockNumber).then(
          () => this.events.next({ message: 'Your CORN harvest is pending', pending: true }),
          error => this.errors.next({ message: 'Error harvesting CORN', error: error })
        );
      });
    });
  }

  compostCorn(amount) {
    this.loadBalances().then(() => {
      var parsedAmount = ethers.utils.parseEther(amount.toString());
      this.corn.compost(this.contracts.walletAddress, parsedAmount).then(
        () => this.events.next({ message: 'Composting CORN is pending', pending: true }),
        error => this.errors.next({ message: 'Error composting CORN', error: error })
      );
    });
  }
}
